<template lang="html">
  <div class="Add-message">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="4" class="font-16">
          <h3>{{$t("common.Post_new_message")}}</h3>
        </Col>
        <Col span="5">

        </Col>
        <Col span="5">

        </Col>
        <Col span="8">
          <Button type="info" class="mgl-10" @click="succ">{{$t("common.Release")}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel">{{$t("common.Cancel")}}</Button>
        </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">{{$t("common.Title")}}：</Col>
      <Col span="6" class="mgl-20"> <Input v-model="ContractName" placeholder="" style="width: 300px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="4" class="text-right">{{$t("common.Content")}}：</Col>
      <Col :md="18" :lg="14"" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{ editorContent }}</p></div>
        <!-- <button @click="getContent">查看内容</button> -->
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg message-upload">
      <Col span="4" class="text-right">{{$t('common.Cover')}}：</Col>
      <Col span="2" class="mgl-20 z-99999">
        <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button  icon="ios-cloud-upload-outline">{{$t('common.Selecte_files')}}</Button>
        </Upload>
      </Col>
      <Col span="4">
        <p class="col-gray mgl-30">{{$t('common.No_files_selected')}}</p>
      </Col>
    </Row>
    <!-- <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">发布时间：</Col>
      <Col span="6" class="mgl-20 text-left"> <DatePicker type="date" placeholder="" style="width: 200px"></DatePicker></Col>
    </Row> -->
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data (){
    return {
      ContractName:'',
      ContractUse:'',
      editorContent: '',
    }
  },
  methods:{
    succ () {
      this.$Message.success(this.$t("common.Save_success"));
      setTimeout(() =>{
        this.$router.push('/Release_message');
      },2000)
    },
    getContent: function () {
        alert(this.editorContent)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.ContractName = routerParams.name
          this.ContractUse = routerParams.use
          this.editorContent = routerParams.content
        }
    },
    cancel () {
      this.$router.push('/Release_message');
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor)
    editor.customConfig.onchange = (html) => {
      //this.editorContent = html
    }
    editor.create()
    this.getParams ()
    if(this.isban == true){
      editor.$textElem.attr('contenteditable', false)
    }

  }
}
</script>

<style lang="css">
.Add-message{
  background-color: #fff;
  padding: 20px;
}
.Add-message .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Add-message .contract-row{
  line-height: 50px;
  margin-bottom: 20px;
}
.Add-message .ivu-select-dropdown{
  z-index: 99999
}
.Add-message .message-upload{
  text-align: left;
  line-height: 45px;
  margin-top: 20px;
}
</style>



